'use client';
import { useEffect, useState } from "react";
import { CheckCircle, XCircle, Loader2 } from 'lucide-react';

export default function QrCodeWhatsApp() {
  const [qr, setQr] = useState('');
  const [loading, setLoading] = useState(true);
  const [connected, setConnected] = useState(false);

  useEffect(() => {
    const fetchQr = async () => {
      try {
        const res = await fetch("fetch(`http://localhost:3333/qrcode?userId=${userId}`)");
        const data = await res.json();
        setConnected(!!data.isConnected);
        setQr(data.qr || ''); // Sempre seta o QR, se tiver
        setLoading(false);
      } catch (e) {
        setQr('');
        setConnected(false);
        setLoading(false);
      }
    };
    fetchQr();
    const interval = setInterval(fetchQr, 2000);
    return () => clearInterval(interval);
  }, []);

  return (
    <div className="flex flex-col items-center justify-center w-full">
      {/* Status da Conexão */}
      <div className="flex items-center gap-2 mb-3">
        {connected ? (
          <>
            <CheckCircle className="text-green-500" size={20} />
            <span className="text-green-600 text-sm font-bold">Conectado ao WhatsApp</span>
          </>
        ) : (
          <>
            <XCircle className="text-red-400" size={20} />
            <span className="text-red-500 text-sm font-bold">Desconectado</span>
          </>
        )}
      </div>

      {/* QR Code */}
      {loading ? (
        <div className="h-60 w-60 bg-slate-100 rounded-xl flex flex-col items-center justify-center text-gray-400 text-xs">
          <Loader2 className="animate-spin mb-2" size={28} />
          Carregando QR Code...
        </div>
      ) : qr && !connected ? (
        <img
          src={qr}
          alt="QR Code do WhatsApp"
          className="w-60 h-60 mx-auto rounded-xl shadow border border-slate-200"
        />
      ) : connected ? (
        <div className="h-60 w-60 bg-green-50 border border-green-200 rounded-xl flex flex-col items-center justify-center">
          <CheckCircle className="text-green-500 mb-1" size={32} />
          <span className="text-green-700 text-sm font-semibold">WhatsApp conectado</span>
        </div>
      ) : (
        // Fallback: caso não tenha QR nem conexão
        <div className="h-60 w-60 bg-slate-100 rounded-xl flex items-center justify-center text-gray-400 text-xs">
          QR não disponível
        </div>
      )}

      {/* Instruções de conexão */}
      <div className="mt-6 p-6 bg-white rounded-2xl border border-slate-100 shadow-sm w-full max-w-lg">
        <span className="block text-lg font-semibold text-gray-700 mb-2">Como conectar:</span>
        <ol className="list-decimal ml-8 text-base text-gray-600 space-y-2">
          <li>Abra o WhatsApp no seu celular</li>
          <li>
            Toque em <span className="font-semibold text-gray-700">Menu</span> (3 pontinhos) {'>'} <span className="font-semibold text-gray-700">Dispositivos conectados</span>
          </li>
          <li>
            Toque em <span className="font-semibold text-gray-700">Conectar um dispositivo</span>
          </li>
          <li>Escaneie o QR code exibido acima</li>
        </ol>
      </div>
    </div>
  );
}
